import React from 'react';
import styled from 'styled-components';
import { ReactComponent as Wave } from '@static/shape-divider.svg';

const WaveWrapper = styled.div`
    ${props =>
        props.accent === 'dark' &&
        `
        background: var(--primary-color)!important;

        svg {
            fill: var(--alt-blue)!important;
        }
    `};

    ${props =>
        props.accent === 'light' &&
        `
        svg {
            fill: white!important;
        }
    `};
    background: var(--alt-blue);

    svg {
        width: 100%;
        fill: #212529;
        margin-bottom: -10px;
    }
`;

export default props => (
    <WaveWrapper {...props}>
        <Wave />
    </WaveWrapper>
);
