import React, { useState } from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import AnchorLink from 'react-anchor-link-smooth-scroll';
/*
import { ReactComponent as Logo } from '@static/logo.svg';
*/
import { Link } from 'gatsby';

import {
    NavContainer,
    Nav,
    NavList,
    NavItem,
    Brand,
    Mobile,
    Hamburger,
} from '@styles/navstyles.js';

/*
const ListLink = props => (
    <NavItem>
        <Link to={props.to}>{props.children}</Link>
    </NavItem>
);
*/

const items = ['About', 'Capital', 'Development', 'Strategy'];

export default function Navbar(props) {
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const toggleClassName = isMenuOpen ? 'hamburger is-open' : 'hamburger';
    const changeMenuColor = isMenuOpen ? 'is-open' : '';

    const Menu = () => {
        return (
            <>
                {items.map(navItem => (
                    <NavItem key={navItem}>
                        <AnchorLink
                            onClick={() => setIsMenuOpen(false)}
                            href={`#${navItem.toLowerCase()}`}
                        >
                            {navItem}{' '}
                        </AnchorLink>
                    </NavItem>
                ))}
                <NavItem>
                    <Link to="/contact/">Contact</Link>
                </NavItem>
            </>
        );
    };

    const data = useStaticQuery(graphql`
        query {
            site {
                siteMetadata {
                    title
                }
            }
        }
    `);
    return (
        <Nav {...props} className={changeMenuColor}>
            <NavContainer>
                <Brand>
                    <Link to="/">{data.site.siteMetadata.title}</Link>
                </Brand>
                <NavList desktop>
                    <Menu />
                </NavList>
            </NavContainer>
            <Mobile>
                <Hamburger
                    className={toggleClassName}
                    onClick={() => setIsMenuOpen(!isMenuOpen)}
                >
                    <span></span>
                    <span></span>
                    <span></span>
                </Hamburger>

                {isMenuOpen && (
                    <NavList>
                        <Menu />
                    </NavList>
                )}
            </Mobile>
        </Nav>
    );
}
